import {ApiParamsKeys, ApiRequestOptions} from '@er/types';


const paramKeys: ApiParamsKeys = {
  sort: '_sort',
  order: '_order',
  size: '_limit',
  page: '_page',
  from: '_start',
  to: '_end'
};

const getResponseResolver = response => {
  if (response) {
    if (response instanceof Array) {
      if (response.length === 1) {
        response = response[0];
      }
    }
    return {success: true, result: response};
  } else {
    return {success: false};
  }
};

const updateResponseResolver = response =>
  response && response.hasOwnProperty('id')
    ? {success: true, result: response.id}
    : {failure: true, message: response};

const deleteResponseResolver = response =>
  response && Object.keys(response).length === 0
    ? {success: true, result: {}}
    : {failure: true, message: response};

export const MockServerApiRequestOptions: ApiRequestOptions = {};
