import {ApiParamsKeys, ApiPayload, ApiRequestOptions, ApiServerType, HttpMethod, HttpRequestPayloadType, Order, Query} from '@er/types';

export interface ErHttpResponse {
  success?: boolean;
  message?: string;
  error?: string;
  content?: any;
  contents?: any[];
}

const paramKeys: ApiParamsKeys = {
  sort: '_sort',
  order: '_order',
  from: 'from',
  size: 'size',
  page: '_page',
  to: '_end'
};

const responseResolver = response => response;

const updateResponseResolver = response =>
  response && response.hasOwnProperty('id')
    ? {success: true, result: response.id}
    : {failure: true, message: response};

const deleteResponseResolver = response =>
  response && Object.keys(response).length === 0
    ? {success: true, result: {}}
    : {failure: true, message: response};

export const ErServerApiRequestOptions: ApiRequestOptions = {
  serverType: ApiServerType.ER,
  paramsKeys: paramKeys,
  actions: {

    login: {
      uriResolver: () => 'login',
      method: HttpMethod.POST,
      notifySuccess: true,
      successMessage: 'ç»å½æå',
      notifyFailure: true,
      onResponse: responseResolver,
      onSuccess: ((resp) => {
        const content = resp.content;
        return content || {};
      })
    },

    logout: {
      uriResolver: () => 'logout',
      notifySuccess: false,
      onResponse: responseResolver
    },

    changePwd: {
      uriResolver: () => 'user/updatePwd',
      notifySuccess: false,
      onResponse: responseResolver
    },

    register: {
      uriResolver: () => 'user/reg',
      method: HttpMethod.POST,
      notifySuccess: true,
      successMessage: 'æ³¨åæå'
    },

    getById: {
      uriResolver: (apiEntry: string, payload) => `${apiEntry}/${payload}`,
      onResponse: (response) => {
        return {
          id: response['_id'],
          ...response['_source']
        };
      }
    },

    getByIds: {
      method: HttpMethod.GET,
      payloadType: HttpRequestPayloadType.PARAMS,
      payloadResolver: (apiEntry: string, payload: ApiPayload, options: ApiRequestOptions) => {
        const _ids = payload; // todo: toString
        if (_ids.length === 0) {
          throw new Error('idå¼ä¸ºç©º');
        }
        const keys = options.paramsKeys;
        return {
          [keys.ids]: _ids,
          [keys.fields]: payload.fields
        };
      }
    },

    getByQuery: {
      method: HttpMethod.GET,
      payloadType: HttpRequestPayloadType.PARAMS,
      payloadResolver: (apiEntry: string, payload: ApiPayload, options: ApiRequestOptions) => {
        let qParams = '';
        const requestPayload = payload || {};
        const query = requestPayload.query;
        if (query) {
          if (query instanceof Array) {
            // (query as Query[]).forEach(q => qParams += `${q.field}=${q.value}&`);
          } else {
            const q = <Query>query;
            // qParams = `${q.field}=${q.value}`;
          }
        }
        const {size, page} = requestPayload;
        if (size || page) {
          if (!qParams.endsWith('&')) {
            qParams += '&';
          }
          qParams += `${options.paramsKeys.size}=${size || 10}&${options.paramsKeys.page}=${page || 1}`;
        }
        const sort = requestPayload.sort;
        if (sort) {
          if (!qParams.endsWith('&')) {
            qParams += '&';
          }
          let sortFields = '';
          let sortOrders = '';
          if (typeof sort === 'string') {
            qParams += `${options.paramsKeys.sort}=${sort}&${options.paramsKeys.order}=${Order.ASC}`;
          } else {
            Object.keys(sort).map(field => {
              sortFields += `${field},`;
              sortOrders += `${sort[field]},`;
            });
            qParams += `${options.paramsKeys.sort}=${sortFields}&${options.paramsKeys.order}=${sortOrders}`;
          }
        }
        return qParams;
      },
      onResponse: response => response
    },

    create: {
      method: HttpMethod.POST,
      onResponse: updateResponseResolver,
      notifySuccess: false,
      alertSuccess: true,
      successMessage: 'æ°æ®ä¿å­æå'
    },

    updateById: {
      method: HttpMethod.PUT,
      payloadType: HttpRequestPayloadType.BODY,
      uriResolver: (apiEntry: string, payload: ApiPayload) => `${apiEntry}/${payload.id}`,
      payloadResolver: (apiEntry: string, payload: ApiPayload) => payload.data,
      onResponse: updateResponseResolver,
      notifySuccess: false,
      alertSuccess: true,
      successMessage: 'æ°æ®æ´æ°æå'
    },

    patchById: {
      method: HttpMethod.PATCH,
      payloadType: HttpRequestPayloadType.BODY,
      uriResolver: (apiEntry: string, payload: ApiPayload) => `${apiEntry}/${payload.id}`,
      payloadResolver: (apiEntry: string, payload: ApiPayload) => payload,
      onResponse: updateResponseResolver,
      notifySuccess: true,
      successMessage: 'æ°æ®æ´æ°æå'
    },

    deleteById: {
      method: HttpMethod.DELETE,
      uriResolver: (apiEntry: string, payload: ApiPayload) => `${apiEntry}/${payload}`,
      onResponse: deleteResponseResolver,
      notifySuccess: true,
      successMessage: 'æ°æ®å é¤æå'
    }

  }
};
