import {ApiParamsKeys, ApiPayload, ApiRequestOptions, ApiServerType, HttpMethod, HttpRequestPayloadType} from '@er/types';
import {DateUtils, EsUtils} from '@er/utils';
import * as esb from 'elastic-builder';

export interface EsHttpResponse {
  _index?: string;
  _type?: string;

  [key: string]: any;
}

const paramKeys: ApiParamsKeys = {
  sort: 'sort',
  from: 'from',
  size: 'size',
  order: 'order',
  page: 'page',
  to: 'to',
  q: 'q',
  query: 'query',
  filter: 'filter',
  globalFiler: 'globalFiler',
  fields: '_source'
};

const responseResolver = response => {
  if (response) {
    if (response instanceof Array) {
      if (response.length === 1) {
        response = response[0];
      }
    }
    return {success: true, content: response};
  } else {
    return {success: false};
  }
};

const updateResponseResolver = response => {
  if (response && response.hasOwnProperty('_id')) {
    const id = response['_id'];
    delete response['_id'];
    return {success: true, content: {id, ...response}};
  } else {
    return {failure: true, message: response};
  }
};

const deleteResponseResolver = response =>
  response && response.result === 'deleted'
    ? {success: true, content: response}
    : {failure: true, message: response};

const parseResponse = (resp) => {
  const response = {};
  response['success'] = resp.hasOwnProperty('hits') || resp.hasOwnProperty('aggregations');
  if (response['success']) {
    response['content'] = {
      total: resp['hits'].total,
      items: resp['hits'].hits.map(hit => {
        return {
          id: hit._id,
          // _index: hit._index,
          // _type: hit._type,
          ...hit['_source']
        };
      })
    };
    if (resp.aggregations) {
      response['content']['aggs'] = resp.aggregations;
    }
    if (resp.highlight) {
      response['content']['highlight'] = resp.highlight;
    }
  } else {
    response['error'] = resp.error && resp.error.reason;
  }
  return response;
};

const getApiEntryWithType = (apiEntry) => {
  if (apiEntry.indexOf('/') === -1) {
    apiEntry = `${apiEntry}/type`;
  }
  return apiEntry;
};

const META_DATA_FILTER_PATH = 'filter_path=hits.hits.*,aggregations.*';

